import React from 'react'
import ReactQuill from 'react-quill'
import 'react-quill/dist/quill.snow.css'
//import { TagsInput } from 'react-tag-input-component' 
import './index.css'
import ChipsArray from "./TagsInput";

function Question() {
  return (
    <div className='add-question'>
        <div className='add-question-container'>
            <div className='head-title'>
                <h1><center>Ask a Question</center></h1>
            </div>
            <div className='question-container'>
              <div className='question-options'>
                  <div className='question-option'>
                      <div className='title'>
                        <h3>Title</h3>
                        <small>
                        Be specific and imaging you're asking a question to another person
                        </small>
                        <input type='text' placeholder='Add Question Title' />
                      </div>
                  </div>
               <div className='question-option'>
               <div className='title'>
                        <h3>Body</h3>
                        <small>
                        Be specific and imaging you're asking a question to another person
                        </small>
                        <ReactQuill className='react-quill' theme='snow'/>
                      </div>
               </div>
               <div className='question-option'>
                      <div className='title'>
                        <h3>Tags</h3>
                        <small> <b>
                        Add up to 5 tags to describe what your question is about</b>
                        </small>
                        
                        {/* <TagsInput name='tags' placeHolder='Press Here to Add New Tag' />*/}
                        <ChipsArray />
                        <button className='button'>Add Your Question</button>
                      </div>
               </div>
              </div>
            </div>
            
        </div>

    </div>
  )
}

export default Question